.profileInfoBlock {
    width: 100%;
    background-color: white;
    padding-bottom: 10px;
    border-radius: 10px;
    margin-bottom: 20px;
    font-family: 'Montserrat', sans-serif;
    -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}

.imagesBlock {
    width: 100%;
}


.backgroundImg {
    width: 100%;
    height: 200px;
    border-radius: 10px;
}
@media screen and (max-width: 850px){
    .backgroundImg {
        height: 150px;
    }
}

.avatar {
    position: relative;
    width: 250px;
    margin: 0 auto;
    top: -120px;
}
@media screen and (max-width: 1000px){
    .avatar {
        width: 170px;
        top: -90px;
    }
}
@media screen and (max-width: 850px){
    .avatar {
        width: 150px;
    }
}

.avatar img {
    position: absolute;
    right: 25px;
    z-index: 1;
    border: 5px solid white;
    width: 200px;
    border-radius: 50%;
    -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}

@media screen and (max-width: 1000px){
    .avatar img {
        width: 150px;
        right: 10px;
    }
}
@media screen and (max-width: 850px){
    .avatar img {
        width: 130px;
    }
}

.avatarEdit {
    position: absolute;
    right: 15%;
    z-index: 1;
    top: 165px;
}

@media screen and (max-width: 1000px){
    .avatarEdit {
        top: 130px;
    }
}
@media screen and (max-width: 850px){
    .avatarEdit {
        top: 112px;
    }
}

.avatarEdit input {
    display: none;
}

.avatarEdit label {
    display: inline-block;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background: #FFFFFF;
    border: 1px solid transparent;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
    cursor: pointer;
    font-weight: normal;
    transition: all .2s ease-in-out;
}

@media screen and (max-width: 1000px){
    .avatarEdit label {
        width: 27px;
        height: 27px;
    }
}
@media screen and (max-width: 850px){
    .avatarEdit label {
        width: 25px;
        height: 25px;
    }
}

.avatarEdit label:hover {
    background: #f1f1f1;
    border-color: #d6d6d6;
}

.avatarEdit label:after {
    content: "âœŽ";
    font-size: 20px;
    color: #757575;
    position: absolute;
    top: 4px;
    left: 0;
    right: 0;
    text-align: center;
    margin: auto;
}

@media screen and (max-width: 1000px){
    .avatarEdit label:after {
        font-size: 17px;
    }
}
@media screen and (max-width: 850px){
    .avatarEdit label:after {
        font-size: 15px;
    }
}

.profileInfo {
    position: relative;
    width: 63%;
    margin: 20px auto;
}
@media screen and (max-width: 1000px){
    .profileInfo {
        margin: 30px auto;
    }
}
@media screen and (max-width: 850px){
    .profileInfo {
        width: 80%;
    }
}

.statusBlock {
    width: 30%;
}


.status {
    width: 100%;
    min-height: 70px;
}
@media screen and (max-width: 1000px){
    .status {
        min-height: 20px;
        font-size: 14px;
    }
}
@media screen and (max-width: 850px){
    .status {
        font-size: 11px;
    }
}
@media screen and (max-width: 450px){
    .status {
        font-size: 9px;
    }
}

.status span {
    word-wrap: break-word;
    cursor: default;
}

.ownerStatus:hover {
    background-color: #f1f3ef;
    cursor: pointer;
}

.lookingJob {
    position: absolute;
    right: 10px;
    color: #42a790;
}
@media screen and (max-width: 1000px){
    .lookingJob {
        font-size: 14px;
    }
}
@media screen and (max-width: 850px){
    .lookingJob {
        font-size: 11px;
    }
}
@media screen and (max-width: 450px){
    .lookingJob {
        font-size: 9px;
    }
}

.statusBlock input {
    width: 100%;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 2px solid #42a790;
    outline: none;
    font-family: 'Montserrat', sans-serif;
}
@media screen and (max-width: 1000px){
    .statusBlock input {
        min-height: 20px;
        font-size: 14px;
    }
}
@media screen and (max-width: 850px){
    .statusBlock input {
        font-size: 11px;
    }
}
@media screen and (max-width: 450px){
    .statusBlock input {
        font-size: 9px;
    }
}

.editButtonContainer{
    width: 40px;
    height: 40px;
    float: right;
    position: relative;
}

.editButton {
    position: absolute;
    right: 0;
    top: 0;
    height: 34px;
    width: 34px;
    color: #757575;
    margin-bottom: 0;
    border-radius: 100%;
    background: #FFFFFF;
    border: 1px solid transparent;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
    cursor: pointer;
    font-weight: normal;
    font-size: 20px;
    transition: all .2s ease-in-out;
}

@media screen and (max-width: 1000px){
    .editButton {
        width: 27px;
        height: 27px;
        font-size: 17px;
    }
}
@media screen and (max-width: 850px){
    .editButton {
        width: 25px;
        height: 25px;
        font-size: 15px;
    }
}

.editButton:hover {
    background: #f1f1f1;
    border-color: #d6d6d6;
}

.editButton:after {
    font-size: 20px;
    color: #757575;
    position: absolute;
    top: 4px;
    left: 0;
    right: 0;
    text-align: center;
    margin: auto;
}

.profileInfoData {
    width: 100%;
}

.majorInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
    margin-left: 40px;
}
@media screen and (max-width: 1000px){
    .majorInfo {
        margin-left: 27px;
    }
}
@media screen and (max-width: 850px){
    .majorInfo {
        margin-left: 25px;
    }
}

.fullName {
    font-size: xx-large;
    margin-bottom: 10px;
    font-weight: bolder;
}
@media screen and (max-width: 1000px){
    .fullName {
        font-size: 25px;
    }
}
@media screen and (max-width: 850px){
    .fullName {
        font-size: 20px;
    }
}
@media screen and (max-width: 450px){
    .fullName {
        font-size: 15px;
    }
}

.skills {
    margin-bottom: 10px;
}
@media screen and (max-width: 1000px){
    .skills {
        font-size: 16px;
    }
}
@media screen and (max-width: 850px){
    .skills {
        font-size: 15px;
    }
}
@media screen and (max-width: 450px){
    .skills {
        font-size: 12px;
    }
}

.showContactsButton {
    width: 100%;
    height: 35px;
    background-color: #42a790;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    border: none;
    font-family: 'Montserrat', sans-serif;
    border-radius: 7px;
    margin-bottom: 20px;
    cursor: pointer;
}
@media screen and (max-width: 1000px){
    .showContactsButton {
        height: 30px;
        font-size: 13px;
    }
}
@media screen and (max-width: 850px){
    .showContactsButton {
        font-size: 11px;
    }
}
@media screen and (max-width: 450px){
    .showContactsButton {
        font-size: 9px;
        height: 25px;
    }
}


.showContactsButton:hover {
    background-color: #3a816e;
}

.contacts {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact{
    margin-bottom: 5px;
}

@media screen and (max-width: 1000px){
    .contact {
        font-size: 13px;
    }
}
@media screen and (max-width: 850px){
    .contact {
        font-size: 11px;
    }
}
@media screen and (max-width: 450px){
    .contact {
        font-size: 9px;
    }
}

.contactLinks {
    margin-bottom: 20px;
}

.link {
    text-decoration: none;
    color: #42a790;
}

.editInfo {
    padding: 20px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30px;
}




